import scroll from '../components/scroll/scroll.vue'
export default {
  name: 'Home',
  components: {
    scroll
  },
  computed: {
    scroll () {
      return this.$refs.scroll
    },
    scroller () {
      return this.$refs.scroll
    }
  },
  data () {
    return {
      tabIdx: 0,
      searchValue: '', // 搜索内容
      courseList: [],  // 课程首页列表
      perPage: 1,  // 页码
      perPageSize: 10,  // 页面加载数
      personInfo: ''  // 个人主页信息
    }
  },
  mounted () {
    let that = this
    that.$nextTick(() => {
      that.getCourseList()
      that.getPersonInfo()
    })
  },
  methods: {
    //  每次刷新或者加载后重新刷新DOM结构
    refresh () {
      this.scroll.refresh()
    },
    // 下拉刷新
    PullDown () {
      setTimeout(() => {
        this.getCourseList()
        this.refresh()
      }, 500)
    },
    // 上拉加载
    PullUp () {
      setTimeout(() => {
        this.perPage++
        this.getCourseList()
        this.refresh()
      }, 500)
    },
    // 获取课程首页列表
    getCourseList () {
      console.log('我的3333333', localStorage.getItem('token'))
      let that = this
      that.axios({
        method: 'POST',
        url: 'interface/Course/column_list',
        data: {
          per_page: that.perPage,  // 页码
          limit: that.perPageSize   // 页面展示数量
        }
      }).then((res) => {
        console.log('课程首页列表', res)
        if (res.err_code !== 1) {
          that.showAlert(res.msg)
          return false
        }
        if (that.perPage === 1) {
          that.courseList = res.data
          that.scroller.finishUpdate(true)
        } else {
          that.courseList = that.courseList.concat(res.data)
          if (res.data.length > 0) {
            that.scroller.finishUpdate(true)
          } else {
            that.scroller.finishUpdate(false)
          }
        }
      })
    },
    // 获取 我的 信息
    getPersonInfo () {
      let that = this
      let data = {
        token: localStorage.getItem('token')
      }
      console.log('我的4444444', data)
      that.axios({
        method: 'POST',
        url: 'interface/Home_page/my',
        data: data
      }).then((res) => {
        console.log('我的', res)
        if (res.code !== 1) {
          this.showAlert(res.msg)
          return false
        }
        that.personInfo = res.data
      })
    },
    // 搜索栏
    inputTyping () {
      let that = this
      console.log(that.searchValue)
      that.axios({
        method: 'POST',
        url: 'interface/Course/course_index_search',
        data: {
          course_title: that.searchValue
        }
      }).then((res) => {
        console.log('搜索结果', res)
        if (res.err_code !== 1) {
          this.showAlert(res.msg)
          return false
        }
        that.courseList = res.data
      })
    },
    // 底部tab栏切换
    toHomePage () {
      this.tabIdx = 0
    },
    toPersonPage () {
      this.tabIdx = 1
      this.getPersonInfo()
    }
  }
}



